export const onBeforeUnloadHandler = e => {
	e.preventDefault();
	e.returnValue = "";
};

export const dirtyChangedEffect = ({
	action: {
		payload: { isDirty }
	},
	updateProperties
}) => {
	if (isDirty) window.addEventListener("beforeunload", onBeforeUnloadHandler);
	else window.removeEventListener("beforeunload", onBeforeUnloadHandler);

	updateProperties({ isDirty });
};

/**
 * This is for DEF0063821. This interceptor is used to conditionally run the effects triggered from
 * now-record-list-connected API actions. We cannot make our API private for reference list only since reference list can
 * be used in different contexts. If the actions do not have fromListModule appened to paylaod, we remove the effect so it is not run,
 * otherwise the effect is run normally.
 */
export const moduleListCheckinterceptor = {
	after(context) {
		const fromModule = get(
			context,
			"coeffects.action.payload.fromModule",
			false
		);
		return {
			...context,
			effects: fromModule ? context.effects : []
		};
	}
};

export const listErrorEffect = ({
	action: {
		payload: { error }
	},
	updateState
}) => {
	updateState({ error });
};
