import "@servicenow/now-record-list-menu-connected";
import "sn-component-em-hidden";
import {
	CONNECTED_TINY_ID_UPDATED,
	DIRTY_CHANGED,
	LIST_CHECKBOX_TOGGLED,
	LIST_ERROR_STATE,
	LIST_LIMIT_UPDATED,
	LIST_MENU_CRUD,
	LIST_MODULE_NOTIFICATION,
	LIST_PAGE_UPDATED,
	LIST_QUERY_UPDATED,
	LIST_REFRESH_REQUESTED,
	LIST_ROW_CLICK_UPDATED,
	LIST_SCROLL_UPDATED,
	LIST_USER_PREFERENCE_WRAP_LIST_TEXT,
	OPEN_PANEL,
	PUSH_CONTENT_UPDATE,
	TOGGLE_LIST_MENU,
	UPDATE_LIST_MENU_QUERY
} from "./constants";
import { PROPERTIES_SET } from "@servicenow/ui-core/lib/constants";
import { createCustomElement } from "@servicenow/ui-core";
import {
	dirtyChangedEffect,
	listCheckboxUpdate,
	listErrorEffect,
	listLimitUpdate,
	listMenuCrud,
	listNotificationUpdate,
	listPageUpdate,
	listPanelUpdate,
	listQueryUpdate,
	listRefreshUpdate,
	listRowClickUpdate,
	listScrollUpdate,
	moduleListCheckinterceptor,
	onBeforeUnloadHandler,
	toggleListMenu,
	updateContentEffect,
	updateListMenuQueryEffect,
	updateTinyUrlEffect
} from "./actions";
import { emptyFeatureFlags } from "@servicenow/now-record-list";
import { getBool, getFeatureFlagsFromProperties } from "./utils";
// eslint-disable-next-line no-unused-vars
import "@servicenow/now-record-list-connected";
import { preWsConfigSysId } from "sn-uxpage-presource";
import { userPreferencesBehavior } from "sn-user-preferences";
import isEmpty from "lodash/isEmpty";
import packageJson from "../../package.json";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import styles from "./_workspaceListModule.scss";

const view = state => {
	const {
		behaviors: { wsConfigSysId },
		componentId,
		error,
		properties,
		properties: {
			alertList,
			columns,
			hideListCreation,
			enableEmHidden,
			isDirty,
			listId,
			selectedListId,
			listMenuCrud,
			listMenuOpen,
			listUpdated,
			menuTab,
			query,
			refreshRequested,
			table,
			targetTable,
			tiny,
			title,
			view
		},
		wordWrap
	} = state;

	const updateListMenuQuery = {
		conditions: query,
		columns,
		table,
		title,
		selectedListSysId: listId
	};

	const tinySessionActiveKey = `tiny_session_${wsConfigSysId}`;
	const tinySessionIdKey = `tiny_id_${wsConfigSysId}`;

	const setTinyActive = isActive => {
		sessionStorage.setItem(tinySessionActiveKey, isActive);
	};

	const setTinyId = tinyId => {
		sessionStorage.setItem(tinySessionIdKey, tinyId);
	};

	const tinySessionActive = sessionStorage.getItem(tinySessionActiveKey);
	const tinySessionId = sessionStorage.getItem(tinySessionIdKey);

	let tinyProp = undefined;

	if (tinySessionActive === null && !isEmpty(tiny)) {
		// new session with a tiny URL
		tinyProp = tiny;
		setTinyActive(true);
		setTinyId(tiny);
	} else if (tinySessionActive === null) {
		// new session without a tiny URL
		setTinyActive(false);
	} else if (!isEmpty(tinySessionId) && tinySessionId !== tiny) {
		// existing session but a new tiny is pasted into the URL
		tinyProp = tiny;
		setTinyActive(true);
		setTinyId(tiny);
	}

	const listFeatureFlags = getFeatureFlagsFromProperties(properties);
	const altHideListCreation = Object.prototype.hasOwnProperty.call(
		listFeatureFlags,
		"hideListCreation"
	)
		? listFeatureFlags["hideListCreation"]
		: hideListCreation;
	return (
		<div className="list-module-wrapper">
			<div className={`list-menu-wrapper ${listMenuOpen ? "" : "-closed"}`}>
				<now-record-list-menu-connected
					component-id={`${componentId}-list-menu`}
					list-id={listId}
					list-menu-crud={listMenuCrud}
					list-menu-open={listMenuOpen}
					listen-to="listMenuCrud:*/LIST_MENU_CRUD"
					workspace-config-id={wsConfigSysId}
					hide-list-creation={altHideListCreation}
					is-dirty={isDirty}
					menu-tab={menuTab}
					tiny={tinyProp}
					is-workspace={true}
				/>
			</div>
			<div className="list-wrapper">
				<now-record-list-connected
					alert-list={alertList}
					component-id={`${componentId}-list`}
					columns={columns}
					error={error}
					list-menu-open={listMenuOpen}
					list-title={title}
					menu-selection={targetTable}
					query={query}
					refresh-requested={refreshRequested}
					selected-list-id={selectedListId}
					table={table}
					tiny={tinyProp}
					view={view}
					hide-list-sharing={false}
					workspace-config-id={wsConfigSysId}
					wordWrap={wordWrap}
					is-dirty={isDirty}
					{...getFeatureFlagsFromProperties(properties)}
					append-to-payload={{ fromModule: true }}
				/>
			</div>
			{enableEmHidden ? (
				<sn-component-em-hidden
					list-updated={listUpdated}
					updateListMenuQuery={updateListMenuQuery}
					append-to-payload={{ fromModule: true }}
				/>
			) : null}
		</div>
	);
};

const transformState = state => {
	const {
		properties: { userPreferences }
	} = state;
	const wordWrapFromUserPref =
		userPreferences.find(
			pref => pref.name === LIST_USER_PREFERENCE_WRAP_LIST_TEXT
		).value || "false";
	let wordWrap = wordWrapFromUserPref.toLowerCase() === "true" ? true : false;
	return {
		...state,
		wordWrap
	};
};

createCustomElement("sn-workspace-list-module", {
	renderer: {
		type: snabbdom,
		view,
		transformState
	},
	properties: {
		alertList: { default: [] },
		query: {},
		columns: {},
		hideListCreation: { default: false },
		enableEmHidden: { default: true },
		active: {
			default: false,
			onChange(newVal, oldVal, dispatch) {
				if (getBool(newVal))
					dispatch(PROPERTIES_SET, {
						refreshRequested: { timestamp: Date.now(), background: true }
					});
			}
		},
		listId: {},
		selectedListId: {},
		listMenuCrud: { default: {} },
		listMenuOpen: { default: true },
		listUpdated: { default: {} },
		menuTab: {},
		refreshRequested: {
			default: {
				timestamp: Date.now(),
				background: false,
				loaderDuration: 0
			}
		},
		table: {},
		targetTable: {},
		tiny: {},
		title: {},
		view: { default: "workspace" },
		isDirty: { default: false }
	},
	styles,
	actionHandlers: {
		[UPDATE_LIST_MENU_QUERY]: {
			effect: updateListMenuQueryEffect
		},
		[CONNECTED_TINY_ID_UPDATED]: {
			effect: updateTinyUrlEffect
		},
		[PUSH_CONTENT_UPDATE]: {
			effect: updateContentEffect
		},
		[LIST_MENU_CRUD]: listMenuCrud,
		[TOGGLE_LIST_MENU]: toggleListMenu,
		[LIST_LIMIT_UPDATED]: {
			effect: listLimitUpdate,
			interceptors: [moduleListCheckinterceptor]
		},
		[LIST_PAGE_UPDATED]: {
			effect: listPageUpdate,
			interceptors: [moduleListCheckinterceptor]
		},
		[LIST_SCROLL_UPDATED]: listScrollUpdate,
		[LIST_ROW_CLICK_UPDATED]: {
			effect: listRowClickUpdate,
			interceptors: [moduleListCheckinterceptor]
		},
		[LIST_QUERY_UPDATED]: {
			effect: listQueryUpdate,
			interceptors: [moduleListCheckinterceptor]
		},
		[LIST_REFRESH_REQUESTED]: {
			effect: listRefreshUpdate,
			interceptors: [moduleListCheckinterceptor]
		},
		[DIRTY_CHANGED]: dirtyChangedEffect,
		[LIST_MODULE_NOTIFICATION]: {
			effect: listNotificationUpdate
		},
		[LIST_ERROR_STATE]: {
			effect: listErrorEffect
		},
		[OPEN_PANEL]: {
			effect: listPanelUpdate,
			stopPropagation: true
		},
		[LIST_CHECKBOX_TOGGLED]: listCheckboxUpdate
	},
	behaviors: [preWsConfigSysId, emptyFeatureFlags, userPreferencesBehavior],
	onConnect(host) {
		if (!window.listComponentRegistry) {
			window.listComponentRegistry = new WeakMap();
		}
		window.listComponentRegistry.set(host, packageJson.version);
	},
	onDisconnect(host) {
		if (window.listComponentRegistry) {
			window.listComponentRegistry.delete(host);
		}
		window.removeEventListener("beforeunload", onBeforeUnloadHandler);
	}
});
