import { LIST_FEATURE_FLAG_PROPERTY_PREFIX } from "./constants";

export const getBool = val => {
	switch (val) {
		case true:
		case "true":
		case 1:
		case "1":
		case "on":
		case "yes":
			return true;
		default:
			return false;
	}
};

export const getFeatureFlagsFromProperties = properties => {
	const props = Object.entries(properties)
		.filter(([flag]) => flag.indexOf("hide") === 0)
		.reduce((prev, [flag, value]) => {
			const sysPropValue = getFeatureFlagValue(flag, value);
			if (typeof sysPropValue === "boolean") prev[flag] = sysPropValue;
			return prev;
		}, {});
	return props;
};

const sysProps = window.ux_globals && window.ux_globals.sysprops;

const getFeatureFlagValue = (flag, value) => {
	const propName = `${LIST_FEATURE_FLAG_PROPERTY_PREFIX}.${flag}`;
	const sysPropValue =
		sysProps && Object.prototype.hasOwnProperty.call(sysProps, propName);
	return sysPropValue ? sysProps[propName] : value;
};
